import ProfileService from '~/services/profile.services';

export default {
    props: {
        createBtn: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        view(id) {
            this.$router.push(`/shop/${id}`);
        },
        edit(id) {
            this.$router.push(`/shop/edit/${id}`);
        },
        async remove(id) {
            await this.profileService.deleteOne({ id });
            await this.profileService.save();
            this.profiles = await this.profileService.find();
        },
        capitalize(string) {
            return string.charAt(0).toUpperCase() + string.slice(1);
        }
    },
    data: () => ({
        profiles: [],
        profileService: null
    }),
    async mounted() {
        console.log(this.createBtn);
        this.profileService = new ProfileService(this.$fireStore);
        this.profiles = await this.profileService.find();
    }
}
